<template>
  <div class="login-wrap" v-loading="loginLoading">
    <img class="bg-login" src="../../assets/images/bg-login.jpg" alt="">
    <el-form class="login-panel" :model="loginForm" label-width="45px">
      <h2 class="text-center">用户登录</h2>
      <el-form-item label="账号:" prop="account">
        <el-input v-model="loginForm.account" @keyup.enter.native="login"></el-input>
      </el-form-item>
      <el-form-item label="密码:" prop="password">
        <el-input v-model="loginForm.password" type="password" @keyup.enter.native="login"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class=" login-btn " @click="login ">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import qs from "qs";
import { setCookie } from "../../util/util";
export default {
  data() {
    return {
      loginForm: {
        account: "",
        password: ""
      },
      loginLoading: false
    };
  },
  methods: {
    login() {
      this.loginLoading = true;
      this.$ajax({
        url: "rest/logLogin/login",
        method: "post",
        data: this.loginForm,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        transformRequest: [
          function(data) {
            return qs.stringify(data);
          }
        ]
      }).then(res => {
        this.loginLoading = false;
        if (res.data.status) {
          setCookie("account", res.data.records.account);
          setCookie("entId", res.data.records.entId);
          this.$store.commit("setUserInfo", res.data.records);
          this.$router.push("/");
        } else {
          this.$message.error(res.data.answerMsg);
        }
      });
    }
  }
};
</script>
<style>
.login-wrap {
  width: 100%;
  height: 100%;
  position: relative;
}
.login-wrap:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(48, 49, 51, 0.12);
  z-index: 1;
}
.bg-login {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.login-panel {
  position: absolute;
  width: 460px;
  height: 300px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px 50px;
  margin: auto;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: rgba(138, 136, 136, 0.1);
  box-shadow: 2px 2px 10px 0px #969696;
}

.login-panel h2 {
  margin: 20px 0 30px;
}

.login-panel .el-form-item {
  margin-bottom: 25px;
}

.login-panel .el-form-item__label {
  /* color: #fff; */
}
.login-panel input {
  list-style: none;
  height: 40px;
  border-radius: 0;
  /* background: transparent; */
}

.login-btn {
  width: 100%;
  height: 36px;
  border-radius: 0;
  background: #418acb;
  color: #fff;
  font-weight: bold;
}
</style>



